<script>
import tuopuInfo from "../../../../static/dailyOperationTuoPuOrder";
import backgroundImage from "@/assets/DailyOperation/background/3-3.jpg";
import FlipClock from "@/components/FlipClock";
import echartsTuoPu from "@/components/echartsTuoPu";
import websockObject from "@/mixins/websock";
export default {
  name: "rightBottom",
  mixins: [websockObject],
  data() {
    return {
      backgroundImage,
      leftErrorInfo: [],
      rightErrorInfo: [],
      leftTime: null,
      rightTime: null
    };
  },
  components: {
    FlipClock,
    echartsTuoPu
  },
  mounted() {
    let _this = this;
    // setInterval(() => {
    //   _this.useTheTestDate()
    // }, 2000)
    this.$nextTick(() => {
      // eslint-disable-next-line no-undef
      _this.wsuri = BaseUrl.dailyRightBottom.websockUrl;
    });
  },
  methods: {
    useTheTestDate() {
      let state = Math.random() > 0.5 ? 1 : 0;
      // console.log(state)
      this.getWebsocketMsg([
        {
          from: "分仓前置",
          to: "DB2",
          belong: "line",
          type: "left",
          state: state
        },
        {
          type: "left",
          belong: "device",
          state: state,
          name: "行情服务器"
        },
        {
          type: "left",
          belong: "device",
          state: state,
          name: "DB"
        },
        {
          type: "left",
          belong: "device",
          state: state,
          name: "期货行情交易服务"
        },
        {
          type: "right",
          belong: "device",
          state: state,
          name: "二级KCXP1"
        },
        {
          type: "right",
          belong: "device",
          state: state,
          name: "二级KCXP2"
        },
        {
          type: "right",
          belong: "device",
          state: state,
          name: "二级KCXP3"
        },
        {
          type: "right",
          belong: "device",
          state: state,
          name: "二级KCXP4"
        }
      ]);
    },
    handerDate(element) {
      let historyDate =
        element.type === "left"
          ? JSON.parse(JSON.stringify(this.leftErrorInfo))
          : JSON.parse(JSON.stringify(this.rightErrorInfo));
      let target = historyDate.findIndex(item => {
        return item.key === element.key;
      });
      if (target === -1 && element.state !== 0) {
        historyDate.push(element);
      }
      if (target !== -1 && element.state === 0) {
        historyDate.splice(target, 1);
      }
      switch (element.type) {
        case "left":
          this.leftErrorInfo = historyDate;
          break;

        default:
          this.rightErrorInfo = historyDate;
          break;
      }
    },
    getWebsocketMsg(sourDate) {
      let toRefresh = [];
      let _this = this;
      if (sourDate.length > 0) {
        sourDate.forEach(element => {
          let ItemKey =
            element.belong === "device"
              ? element.name
              : element.from + "To" + element.to;
          element.key = ItemKey;
          this.handerDate(element);
          if (!toRefresh.includes(element.type)) toRefresh.push(element.type);
        });
      }
      if (toRefresh.includes("left")) {
        this.leftTime && clearTimeout(this.leftTime);
        this.leftTime = setTimeout(() => {
          _this.$refs.left.initChart(this.leftErrorInfo);
        }, 500);
      }
      if (toRefresh.includes("right")) {
        this.rightTime && clearTimeout(this.rightTime);
        this.rightTime = setTimeout(() => {
          _this.$refs.right.initChart(this.rightErrorInfo);
        }, 500);
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    }
  },
  render(h) {
    return (
      <section
        class="mainBox"
        {...{
          style: { backgroundImage: " url(" + this.backgroundImage + ")" }
        }}
      >
        <section class="contentBox flexBox">
          <section class="flexBoxItem">
            <div class="title">{tuopuInfo[4].title}</div>
            <section class="chartsBox">
              <echartsTuoPu
                ref="left"
                dataType={tuopuInfo[4].node}
              ></echartsTuoPu>
            </section>
          </section>
          <section
            class="flexBoxItem"
            {...{
              style: { position: "relative" }
            }}
          >
            <div class="title">{tuopuInfo[5].title}</div>
            <section
              class="chartsBox"
              {...{
                style: {
                  width: "1100px",
                  position: "absolute",
                  left: "-170px",
                  top: "21px",
                  height: "960px"
                }
              }}
            >
              <echartsTuoPu
                ref="right"
                dataType={tuopuInfo[5].node}
              ></echartsTuoPu>
            </section>
          </section>
        </section>
      </section>
    );
  }
};
</script>

<style scoped>
.timeClock {
  position: absolute;
  right: 50px;
  top: 245px;
}
.contentBox {
  width: 1800px;
  position: absolute;
  top: 0;
  left: 40px;
}
.flexBox {
  display: flex;
}
.flexBoxItem {
  width: 50%;
  height: 960px;
  padding: 15px;
  box-sizing: border-box;
}
.title {
  font-size: 30px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #e6f4ff;
  text-align: left;
}
.chartsBox {
  width: 100%;
  height: 100%;
}
</style>



